import cartControl from '../../components/cartControl/cartControl.vue';
import { cartControlMixin } from '../../common/mixins.js';
import { mapGetters } from 'vuex';

export default {
	data() {
		return {};
	},
	mixins: [cartControlMixin],
	props: {
		selectGoods: {
			tyep: Array,
			default: () => []
		},
		totalCount: {
			type: Number,
			default: 0
		},
		fold: {
			type: Boolean,
			default: true
		}
	},
	computed: {
		...mapGetters(['watchOption']),
		listShow() {
			let that = this;
			if (!this.totalCount) {
				this.$emit('parentFold');
				return false;
			}
			let show = !that.fold;
			return show;
		}
	},
	components: {
		cartControl
	},
	methods: {
		empty() {
			let that = this
			uni.removeStorage({
				key: 'goodsList',
				success: function(res) {
					that.setWatchOption(!that.watchOption)
					that.$emit('parentFold');
				}
			});
		},
		hideList() {
			this.$emit('parentFold');
		},
		moveHandle() {}
	}
};
